import {
    BufferGeometry,
    Intersection, Material,
    Points,
    Raycaster,
    Sphere,
    Vector3
} from "three";
import { WVCamera } from "../Rendering/WVCamera";

// This points object is aware of its points size in world space and raycasts them correctly
// PointMaterial.size is used instead of Raycaster.params.Points.threshold
export class RaycastablePoints<TMaterial extends Material | Material[]> extends Points {

    private sphere = new Sphere();
    private vec3 = new Vector3();

    constructor(protected _camera: WVCamera, protected _container: HTMLElement, public pointSize?: number, material?: TMaterial) {
        super(new BufferGeometry(), material);
    }

    public raycast(raycaster: Raycaster, intersects: Intersection[]): void {
        const geometry = this.geometry;
        const matrixWorld = this.matrixWorld;

        // Checking boundingSphere distance to ray
        if (geometry.boundingSphere === null) geometry.computeBoundingSphere();
        this.sphere.copy(geometry.boundingSphere);
        this.sphere.applyMatrix4(matrixWorld);
        this.sphere.radius += this._getWorldSize(this.sphere.center.distanceTo(this._camera.position) + this.sphere.radius);
        if (raycaster.ray.intersectsSphere(this.sphere) === false) return;

        const posAttribute = this.geometry.attributes.position;
        for (let i = 0, l = posAttribute.count; i < l; i++) {
            const position = this.vec3.fromArray(posAttribute.array, i * 3).applyMatrix4(matrixWorld);
            this._raycastPoint(position, i, raycaster, intersects);
        }
    }

    private _getWorldSize(distance: number): number {
        return this._camera.getViewWorldSize(distance) * this.pointSize / this._container.clientWidth;
    }

    private _raycastPoint(point: Vector3, index: number, raycaster: Raycaster, intersects: Intersection[]): void {
        const size = this._getWorldSize(point.distanceTo(this._camera.position));

        const rayPointDistanceSq = raycaster.ray.distanceSqToPoint(point);
        if (rayPointDistanceSq < size * size) {
            const intersectPoint = raycaster.ray.closestPointToPoint(point, this.vec3);
            const distance = raycaster.ray.origin.distanceTo(intersectPoint);

            if (distance < raycaster.near || distance > raycaster.far) return;

            intersects.push({
                distance: distance,
                point: this.vec3.clone(),
                index: index,
                face: null,
                object: this,
            });
        }
    }

}
