import {PointIconModel} from "./PointIconModel";
import {Scene, Vector3} from "three";
import {Api} from "@technology/webgl-viewer/dist/esnext/Api";
import {WebglViewerPlugin} from "@technology/webgl-viewer/dist/esnext/WebglViewerPlugin";
import {WebglViewerEventName} from "@technology/webgl-viewer/dist/esnext/common";
import {RenderPass} from "three/examples/jsm/postprocessing/RenderPass";

export interface PointIcon {
    id: number;
    iconPath: string;
    position: Vector3;
    size: number;
}

export interface IconsSettings {
    clusterRadius: number;
}

export class IconsPlugin extends WebglViewerPlugin {
    private _api: Api;
    private _data: PointIcon[] = [];
    protected _model: PointIconModel;
    settings: IconsSettings;

    get name(): string {
        return "icons";
    }

    constructor(settings?: IconsSettings) {
        super();
        this.settings = settings || {clusterRadius: 120};
    }

    set api(api: Api) {
        this._api = api;
    }

    getIcons(): PointIcon[] {
        return this._data;
    }

    private getModel(): PointIconModel {
        if (this._model) return this._model;

        this._model = new PointIconModel(
            this._data,
            this._api.camera,
            this._api.container,
            this._api.renderingManager,
            this.settings
        );
        this._api.models.add(this._model, false);
        this.createRenderPass();
        this._api.eventDispatcher.subscribe(WebglViewerEventName.NavigationEnd, () => this._model.update());
        return this._model;
    }

    private createRenderPass(): void {
        const scene = new Scene();
        scene.add(this._model);
        const pass = new RenderPass(scene, this._api.camera);
        pass.clear = false;
        this._api.renderingManager.composer.addPassAfterSelection(pass);
    }

    public async add(pointIcon: PointIcon | PointIcon[]): Promise<void> {
        const objects = pointIcon instanceof Array ? pointIcon : [pointIcon];
        objects.forEach(p => this._data.push(p));
        await this.getModel().updateIconTextureAtlas();
        this.getModel().update();
    }

    public async remove(pointIcon: PointIcon | PointIcon[]): Promise<void> {
        const objects = pointIcon instanceof Array ? pointIcon : [pointIcon];
        objects.forEach(p => this._data.splice(this._data.indexOf(p), 1));
        await this.getModel().updateIconTextureAtlas();
        this.getModel().update();
    }

    public clear(): void {
        this._data.length = 0;
        this.getModel().update();
    }
}
