import {BehaviorSubject} from "rxjs";
import {Box3, Line, Matrix4, Mesh, Object3D, Points} from "three";
import {IIntersection} from "./Picker/IIntersection";
import {Caster} from "./Picker/Caster";
import {PointerInput} from "./InputHandler";
import {SnapType} from "./common";
import {Vector3Const} from "./Helpers/common-utils";

export type GeometryObject3D = Mesh | Points | Line;

export interface SelectableModel extends PickableModel {
    readonly isSelectable: true;
    areaPick(caster: Caster, containedOnly?: boolean): Promise<IIntersection>;
    setSelection(ids: number[]): void;
    clearSelection(): void;
}

export interface HoverableModel {
    setHoveredFromEvent(event: PointerInput): void;
}

export interface PickableModel {
    pick(caster: Caster): Promise<IIntersection>;
}

export interface SnappedPickableModel extends PickableModel {
    pickSnapped(caster: Caster, snapTypes: SnapType[]): Promise<IIntersection[]>;
}

export abstract class Model extends Object3D {
    get modelId(): string {
        return this.name;
    }

    protected boundingBox: BehaviorSubject<Box3>;

    constructor(name: string) {
        super();
        super.name = name;
        this.boundingBox = new BehaviorSubject(new Box3());
        this.up.copy(Vector3Const.up);
    }

    getBoundingBox(ids?: number[]): Promise<Box3> {
        return Promise.resolve(this.boundingBox.value);
    }

    subscribeToBoundingBox(callback: (box: Box3) => void): void {
        this.boundingBox.subscribe(callback);
    }

    transform(matrix: Matrix4): void {
        const newTransform = new Matrix4().fromArray(matrix.elements);
        const oldTransform = this.matrixWorld.clone();
        newTransform.decompose(this.position, this.quaternion, this.scale);

        const bbTransform = newTransform.multiply(oldTransform.invert());
        this.boundingBox.value.applyMatrix4(bbTransform);

        this.updateMatrixWorld(true);
        this.boundingBox.next(this.boundingBox.value);
    }

    isLoading(): boolean {
        return false;
    }

    dispose(): void {};
}
