import { Model, PickableModel } from "@technology/webgl-viewer/dist/esnext/Model";
import { IIntersection } from "@technology/webgl-viewer/dist/esnext/Picker/IIntersection";
import {
    Box3,
    Texture,
    Vector3
} from "three";
import { Caster } from "@technology/webgl-viewer/dist/esnext/Picker/Caster";
import { WVCamera } from "@technology/webgl-viewer/dist/esnext/Rendering/WVCamera";
import { RenderingManager } from "@technology/webgl-viewer/dist/esnext/Rendering/RenderingManager";
import { PointIconBatch } from "./PointIconBatch";
import {IconsSettings, PointIcon} from "./IconsPlugin";

export class PointIconModel extends Model implements PickableModel {
    static get ModelId(): string { return "icons" };

    private readonly batchSize = 400;
    private readonly badgeAtlasSize = 11;
    private readonly iconSizeInAtlas = 128;
    private iconPaths: string[];
    private badgeTextureAtlas: Texture;
    private iconsAtlas = new Texture();
    private canvas2d = document.createElement("canvas");
    private context2d = this.canvas2d.getContext("2d");
    private updatingAtlas = false;
    private vec3 = new Vector3();

    constructor(
        private _data: PointIcon[],
        private _camera: WVCamera,
        private _container: HTMLElement,
        private _renderingManager: RenderingManager,
        private _settings: IconsSettings
    ) {
        super(PointIconModel.ModelId);
        this.badgeTextureAtlas = this.createBadgeTextureAtlas();
    }

    private get batches(): PointIconBatch[] {
        return this.children as PointIconBatch[];
    }

    pick = (() => {
        return (caster: Caster): Promise<IIntersection> => {
            if (this.batches.length === 0)
                return;

            const intersection = caster.intersectObjects(this.batches.map(b => b.icons), false)[0];
            if (intersection) {
                const cluster = this.batches.find(b => b.icons === intersection.object).getCluster(intersection.index);
                const result = { model: this, id: cluster.mainIcon.id, childrenIds: cluster.iconIds, object: null, ray: caster.ray, pickPriority: 1 } as IIntersection;
                return Promise.resolve(Object.assign(result, intersection));
            }
        };
    })();

    private createBadgeTextureAtlas(): Texture {
        const size = 64;
        const canvas2d = document.createElement("canvas");
        const context2d = canvas2d.getContext("2d");

        canvas2d.width = size * this.badgeAtlasSize;
        canvas2d.height = size;

        context2d.font = (size * 0.8) + "px Arial";
        context2d.textAlign = "center";
        context2d.textBaseline = "middle";
        for (let i = 0; i < this.badgeAtlasSize; i++) {
            context2d.fillStyle = "white";
            context2d.beginPath();
            context2d.rect(size * i, 0, size, size);
            context2d.fill();
            context2d.lineWidth = 6;
            context2d.strokeStyle = "gray";
            context2d.stroke();
            context2d.fillStyle = "black";
            context2d.fillText(i === this.badgeAtlasSize - 1 ? "" : i.toString(), size * (i + 0.5), size * 0.5 + 5);
        }
        const texture = new Texture(canvas2d);
        texture.needsUpdate = true;
        return texture;
    }

    public update(): void {
        if (this._data.length === 0) {
            this.clear();
            this._renderingManager.redraw();
            return;
        }

        for (const batch of this.batches)
            batch.clearData();
        for (const icon of this._data) {
            const batchPosition = this.vec3.copy(icon.position).divideScalar(this.batchSize).round().multiplyScalar(this.batchSize);
            let batch = this.batches.find(b => b.position.distanceToSquared(batchPosition) < 0.01);
            if (!batch) {
                batch = new PointIconBatch(
                    this._camera,
                    this._container,
                    this._renderingManager,
                    this.badgeTextureAtlas,
                    this.badgeAtlasSize,
                    this.iconsAtlas,
                    this.iconPaths,
                    this._settings
                );
                batch.position.copy(batchPosition);
                this.add(batch);
            }
            batch.addIconData(icon);
        }
        for (const batch of this.batches) {
            if (!batch.isEmpty()) batch.update();
            else this.remove(batch);
        }
        this.computeBoundingBox();
        this._renderingManager.redraw();
    }

    public async updateIconTextureAtlas(): Promise<void> {
        const iconPaths = Array.from(new Set(this._data.map(p => p.iconPath)));
        if (this.iconPaths && !iconPaths.find(p => !this.iconPaths.includes(p)) && this.iconPaths.length === iconPaths.length)
            return;

        if (this.updatingAtlas) throw new Error("Unable to update atlas texture is parallel, make sure you are awaiting the Promise");
        this.updatingAtlas = true;

        if (!this.iconPaths) {
            this.iconPaths = iconPaths;
        }
        else {
            this.iconPaths.length = 0;
            this.iconPaths.push(...iconPaths);
        }

        this.canvas2d.width = this.iconSizeInAtlas * iconPaths.length;
        this.canvas2d.height = this.iconSizeInAtlas;

        for (let i = 0; i < iconPaths.length; i++)
            this.context2d.drawImage(await this.createImage(iconPaths[i]), this.iconSizeInAtlas * i, 0, this.iconSizeInAtlas, this.iconSizeInAtlas);

        this.iconsAtlas.image = this.canvas2d;
        this.iconsAtlas.needsUpdate = true;
        this.updatingAtlas = false;
    }

    private async createImage(url: string): Promise<HTMLImageElement> {
        return new Promise<HTMLImageElement>((resolve) => {
            const img = new Image();
            img.onload = () => resolve(img);
            img.crossOrigin = "anonymous";
            img.src = url;
        });
    }

    private computeBoundingBox(): void {
        this.boundingBox.value.makeEmpty();

        const tempBox = new Box3();
        this.batches.forEach(batch => {
            if (batch.icons.geometry.boundingBox) {
                tempBox.copy(batch.icons.geometry.boundingBox).min.add(batch.position);
                tempBox.max.add(batch.position);
                this.boundingBox.value.union(tempBox);
            }
        });

        this.boundingBox.next(this.boundingBox.value);
    }
}
