import { Vector2, Vector3 } from "three";
import {IconsSettings, PointIcon} from "./IconsPlugin";
import {WVCamera} from "@technology/webgl-viewer/dist/esnext/Rendering/WVCamera";

export class PointIconClusterer {
    private _clusters: PointIconCluster[] = [];
    private _clustersCount: number;
    private positions: Float32Array;
    private tmpPosition = new Vector3();
    private tmpScreenPosition = new Vector3();

    constructor(private camera: WVCamera, private container: HTMLElement, private settings: IconsSettings) {
    }

    get clustersCount(): number {
        return this._clustersCount;
    }

    public addPoint(point: PointIcon, parentPosition: Vector3): void {
        this.tmpPosition.x = point.position.x; this.tmpPosition.y = point.position.y; this.tmpPosition.z = point.position.z;
        this.tmpScreenPosition.copy(this.tmpPosition);
        this.tmpPosition.sub(parentPosition);
        this.tmpScreenPosition.project(this.camera);
        this.tmpScreenPosition.x = (1 + this.tmpScreenPosition.x) * this.container.clientWidth * 0.5;
        this.tmpScreenPosition.y = (1 - this.tmpScreenPosition.y) * this.container.clientHeight * 0.5;

        if (this.tmpScreenPosition.x < 0 || this.tmpScreenPosition.x > this.container.clientWidth || this.tmpScreenPosition.y < 0 || this.tmpScreenPosition.y > this.container.clientHeight) return;

        let cluster = this.findClosestCluster(this.tmpScreenPosition);
        if (cluster) {
            cluster.add(point.id, this.tmpPosition, this.tmpScreenPosition);
        }
        else if (this.positions.length >= (this._clustersCount + 1) * 3) {
            if (this._clusters.length > this._clustersCount)
                cluster = this._clusters[this._clustersCount];
            else {
                cluster = new PointIconCluster(this._clustersCount);
                this._clusters.push(cluster);
            }
            cluster.reset(point, this.positions, this.tmpPosition, this.tmpScreenPosition);
            this._clustersCount++;
        }
    }

    private findClosestCluster(screenPosition: Vector3): PointIconCluster {
        if (this._clustersCount === 0) return null;

        function distance(a: Vector2, b: Vector3): number {
            return Math.abs(a.x - b.x) + Math.abs(a.y - b.y);
        }

        let closest: PointIconCluster;
        let prev: PointIconCluster = null;
        for (let i = 0; i < this._clustersCount; i++) {
            const cluster = this._clusters[i];
            const prevD = prev ? distance(prev.screenPosition, screenPosition) : 999999;
            const currD = distance(cluster.screenPosition, screenPosition);
            if (prevD < this.settings.clusterRadius || currD < this.settings.clusterRadius)
                closest = currD < prevD && currD < this.settings.clusterRadius? cluster : prev;
            prev = cluster;
        }
        return closest;
    }

    public fillBadgeAttributes(positions: Float32Array, uvs: Float32Array, atlasSize: number): number {
        let index = 0;
        const badgeSize = 1 / atlasSize;
        for (let i = 0; i < this.clustersCount; i++) {
            const c = this._clusters[i];
            if (c.iconsCount > 1) {
                const ip = index * 3;
                positions[ip] = c.x;
                positions[ip + 1] = c.y;
                positions[ip + 2] = c.z;

                const io = index * 4;
                uvs[io] = Math.min(c.iconsCount, atlasSize - 1) * badgeSize;
                uvs[io + 1] = 0;
                uvs[io + 2] = uvs[io] + badgeSize;
                uvs[io + 3] = 1;
                index++;
            }
        }
        return index;
    }

    public getCluster(index: number): PointIconCluster {
        return this._clusters[index];
    }

    public reset(positions: Float32Array): void {
        this._clustersCount = 0;
        this.positions = positions;
    }
}

export class PointIconCluster {
    public iconsCount: number = 1;
    public mainIcon: PointIcon;
    public iconIds: number[] = [];


    private _screenPosition = new Vector2();
    private positions: Float32Array;
    private readonly index: number;

    constructor(i: number) {
        this.index = i * 3;
    }

    get screenPosition(): Vector2 {
        return this._screenPosition;
    }

    reset(mainIcon: PointIcon, positions: Float32Array, worldPosition: Vector3, screenPosition: Vector3): void {
        this.positions = positions;
        this.iconsCount = 1;
        this.iconIds = [mainIcon.id];
        this._screenPosition.x = screenPosition.x;
        this._screenPosition.y = screenPosition.y;
        this.x = worldPosition.x;
        this.y = worldPosition.y;
        this.z = worldPosition.z;
        this.mainIcon = mainIcon;
    }

    add(clusteredIconId: number, worldPosition: Vector3, screenPosition: Vector3): void {
        this.x = (this.x * this.iconsCount + worldPosition.x) / (this.iconsCount + 1);
        this.y = (this.y * this.iconsCount + worldPosition.y) / (this.iconsCount + 1);
        this.z = (this.z * this.iconsCount + worldPosition.z) / (this.iconsCount + 1);
        this._screenPosition.x = (this._screenPosition.x * this.iconsCount + screenPosition.x) / (this.iconsCount + 1);
        this._screenPosition.y = (this._screenPosition.y * this.iconsCount + screenPosition.y) / (this.iconsCount + 1);
        this.iconIds.push(clusteredIconId);
        this.iconsCount++;
    }

    set x(v: number) { this.positions[this.index] = v; }
    get x(): number { return this.positions[this.index]; }
    set y(v: number) { this.positions[this.index + 1] = v; }
    get y(): number { return this.positions[this.index + 1]; }
    set z(v: number) { this.positions[this.index + 2] = v; }
    get z(): number { return this.positions[this.index + 2]; }
}
