import {
    BufferAttribute,
    BufferGeometry,
    Points,
    Texture,
    Vector2,
    Object3D, Material
} from "three";
import { PointIconClusterer, PointIconCluster } from "./PointIconClusterer";
import { WVCamera } from "@technology/webgl-viewer/dist/esnext/Rendering/WVCamera";
import { createGeometryAttribute } from "@technology/webgl-viewer/dist/esnext/Helpers/utils";
import { RaycastablePoints } from "@technology/webgl-viewer/dist/esnext/CustomObjects/RaycastablePoints";
import { WVPointsMaterial } from "@technology/webgl-viewer/dist/esnext/Rendering/WVPointsMaterial";
import { GlobalMaterialUniforms, RenderingManager } from "@technology/webgl-viewer/dist/esnext/Rendering/RenderingManager";
import {IconsSettings, PointIcon} from "./IconsPlugin";

export class PointIconBatch extends Object3D {
    icons: RaycastablePoints<Material>;
    private badges: Points;
    private _data: PointIcon[];
    private _clusterer: PointIconClusterer;

    constructor(
        private _camera: WVCamera,
        private _container: HTMLElement,
        private _renderingManager: RenderingManager,
        private badgeTextureAtlas: Texture,
        private badgeAtlasSize: number,
        private iconsAtlas: Texture,
        private iconPaths: string[],
        private settings: IconsSettings
    ) {
        super();
        this._data = [];
        this._clusterer = new PointIconClusterer(this._camera, this._container, this.settings);
    }

    clearData(): void {
        this._data = [];
    }

    addIconData(icon: PointIcon): void {
        this._data.push(icon);
    }

    isEmpty(): boolean {
        return this._data.length === 0;
    }

    public update(): void {
        if (!this.icons) this.createIcons(this._data[0]);
        if (!this.badges) this.createBadges(this._data[0]);

        this.updateGeometry(this._data);
    }

    private getGlobalUniformsWithoutOpacity(): GlobalMaterialUniforms {
        const globalUniforms = Object.assign({}, this._renderingManager.uniforms);
        // Ignore global opacity
        delete globalUniforms.globalOpacity;
        delete globalUniforms.globalTransparent;
        return globalUniforms;
    }

    private createIcons(refObj: PointIcon): void {
        const material = new WVPointsMaterial({
            size: refObj.size,
            map: this.iconsAtlas,
            transparent: true,
            clippingPlanes: this._renderingManager.clippingPlanes
        }, this.getGlobalUniformsWithoutOpacity());
        material.depthTest = false;
        material.depthWrite = false;
        const pointsObject = new RaycastablePoints<Material>(this._camera, this._container, refObj.size, material);

        this.add(pointsObject);
        this.icons = pointsObject;
    }

    private createBadges(refObj: PointIcon): void {
        const material = new WVPointsMaterial({
            size: 16,
            map: this.badgeTextureAtlas,
            offset: new Vector2(0, refObj.size * 1.4),
            transparent: true,
            clippingPlanes: this._renderingManager.clippingPlanes
        }, this.getGlobalUniformsWithoutOpacity());
        material.depthTest = false;
        material.depthWrite = false;
        const geometry = new BufferGeometry();
        const pointsObject = new Points(geometry, material);

        this.add(pointsObject);
        this.badges = pointsObject;
    }

    private fillIconUVs(uvs: Float32Array): void {
        for (let i = 0; i < this._clusterer.clustersCount; i++) {
            const icon = this._clusterer.getCluster(i).mainIcon;
            const iconIndex = this.iconPaths.indexOf(icon.iconPath);
            const io = i * 4;
            uvs[io] = iconIndex / this.iconPaths.length;
            uvs[io + 1] = 0;
            uvs[io + 2] = (iconIndex + 1) / this.iconPaths.length;
            uvs[io + 3] = 1;
        }
    }

    getCluster(index: number): PointIconCluster {
        return this._clusterer.getCluster(index);
    }

    private updateGeometry(data: PointIcon[]): void {
        const length = data.length;
        const iconsGeometry = this.icons.geometry;
        const iconsPositions = createGeometryAttribute(iconsGeometry, "position", Math.min(data.length, 500), 3, Float32Array).array as Float32Array;

        const clusterer = this._clusterer;
        clusterer.reset(iconsPositions);
        for (let i = 0; i < length; i++)
            clusterer.addPoint(data[i], this.position);

        {
            const posAttr = iconsGeometry.getAttribute("position") as BufferAttribute;
            const uvAttr = createGeometryAttribute(iconsGeometry, "pointUv", clusterer.clustersCount, 4, Float32Array);
            this.fillIconUVs(uvAttr.array as Float32Array);
            posAttr.count = clusterer.clustersCount;
            posAttr.updateRange.count = clusterer.clustersCount * 3;
            posAttr.needsUpdate = true;
            uvAttr.count = clusterer.clustersCount;
            uvAttr.updateRange.count = clusterer.clustersCount * 4;
            uvAttr.needsUpdate = true;
            iconsGeometry.computeBoundingSphere();
            iconsGeometry.computeBoundingBox();
        }
        {
            const badgesGeometry = this.badges.geometry;
            const posAttr = createGeometryAttribute(badgesGeometry, "position", clusterer.clustersCount, 3, Float32Array);
            const uvAttr = createGeometryAttribute(badgesGeometry, "pointUv", clusterer.clustersCount, 4, Float32Array);
            const badgeCount = clusterer.fillBadgeAttributes(posAttr.array as Float32Array, uvAttr.array as Float32Array, this.badgeAtlasSize);
            posAttr.count = badgeCount;
            posAttr.updateRange.count = badgeCount * 3;
            posAttr.needsUpdate = true;
            uvAttr.count = badgeCount;
            uvAttr.updateRange.count = badgeCount * 4;
            uvAttr.needsUpdate = true;
        }
    };
}
